	module.exports = {"data":{"post":{"html":"<p>Boys weekend home alone. Mama took off Friday for the second part of her convention and gets back today at noon. Christopher, do we have any cleaning up to do this morning? Nah....</p>\n<p>Christopher and I went downtown yesterday. My favorite tech bookstore now has a corner with a rocking horse and Legos. To each their own toys.</p>\n<p><a href=\"http://shanson.editthispage.com/discuss/msgReader$578?mode=day\">André</a> says he and Andrea might go to Amsterdam as well.</p>","fields":{"title":"42","date":"13 May 2001","url":"https://papascott.de/2001-05-13-42/"}}},"pathContext":{"slug":"/2001-05-13-42/","prev":{"fields":{"title":"Weekend","date":"11 May 2001","slug":"/2001-05-11-weekend/"}},"next":{"fields":{"title":"Tired boy","date":"14 May 2001","slug":"/2001-05-14-tired-boy/"}}}}

